@import "../../styles/variables";

.ask {
  background-color: black;
  text-align: center;
  color: white;
  font-size: 2em;
  font-family: $font-family-title, sans-serif;
  line-height: 1.5em;
  padding: 50px 0 10px;

  &__title {
    font-size: 1em;
  }

  &__box {
    display: flex;
    height: 65vh;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  &__image {
    position: absolute;
    height: 290px;
    top: 50%;
    transform: translateY(-50%);
    left: 10%;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__form {
    flex: 0 1 50%;
  }

  .form {
    &__label {
      font-size: 0.8em;
      text-align: center;
      font-family: $font-family-subtitle;
      font-weight: 400;
      line-height: 2em;
    }
    &__input {
      input {
        width: 60%;
        font-size: 0.7em;
        padding: 5px 7px;
        border-radius: 5px;
        margin-bottom: 10px;
      }
    }

    &__btn {
      margin: 30px 0;
    }
  }
}

